.navbar-toggler {
  width: 1.5em;
  height: 1.5em;
  padding: 0.25em !important;
  background-color: transparent;
  border-color: rgba(0, 0, 0, 0) !important;
  color: transparent;
  z-index: 1070; }
  .navbar-toggler .hamburger {
    color: transparent; }
  .navbar-toggler .line {
    width: 100%;
    height: 0.1875em;
    border-radius: 0.5rem;
    background-color: #d46332;
    margin-top: 10%; }
  .navbar-toggler:focus {
    outline: none; }
  .navbar-toggler:hover {
    transition: all 0.8s ease-in-out; }
    .navbar-toggler:hover .line:nth-child(1) {
      width: 100%; }
    .navbar-toggler:hover .line:nth-child(2) {
      width: 100%; }
    .navbar-toggler:hover .line:nth-child(3) {
      width: 100%; }
  .navbar-toggler[aria-expanded="true"] {
    transform: rotate(-180deg);
    transition: all 0.8s ease-in-out; }
    .navbar-toggler[aria-expanded="true"] .line {
      transition: all 0.8s ease-in-out; }
      .navbar-toggler[aria-expanded="true"] .line:nth-child(1) {
        width: 100%;
        transform: translateY(150%) rotate(45deg);
        margin: 0; }
      .navbar-toggler[aria-expanded="true"] .line:nth-child(2) {
        opacity: 0;
        width: 100%; }
      .navbar-toggler[aria-expanded="true"] .line:nth-child(3) {
        width: 100%;
        transform: translateY(-150%) rotate(-45deg); }

.navbar-brand {
  font-weight: bold;
  color: #2D3142;
  z-index: 1070;
  transition: all 0.8s; }
  .navbar-brand[aria-expanded="true"] {
    color: #FBFBFD; }

.brand_and_logo_main_2 {
  text-align: center; }
  .brand_and_logo_main_2 .navbar-brand {
